/* eslint-disable react/forbid-prop-types */
/* eslint-disable react/no-array-index-key */
/* eslint-disable react/no-unescaped-entities */
import React from "react"
import PropTypes from "prop-types"
import styled from "styled-components"
import tw from "tailwind.macro"
import { StaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import LinkTo from "../components/LinkTo"
import Layout from "../components/layout"
import SEO from "../components/seo"
import AppWrapper from "../views/AppWrapper"
import Content from "../elements/Content"
import Hero from "../views/Hero"
import CloseIcon from "../elements/CloseIcon"
import { H1Light, H3, H5, P, PLight, Highlight } from "../elements/Text"

const SectionWrapper = styled.div`
  ${tw`lg:flex lg:flex-wrap`}
`

const SectionContainer = styled.div`
  ${tw`lg:w-1/2 xl:w-1/3 mb-8 text-center`}
`

const SectionHeader = styled(H3)`
  ${tw`mb-0 text-center`};
`

const Separator = styled.span`
  ${tw`w-1/4 bg-pink h-2 mx-auto block`};
`

const SectionContents = styled.div`
  ${tw``};
`

const BigTitle = styled(H1Light)`
  ${tw`text-5xl lg:text-7xl`}
`

const Name = styled.span`
  ${tw`block mt-4`};
`

const Title = styled(H5)`
  ${tw`mt-2 mb-0 text-grey`};
`

const CloseWrapper = styled.div`
  ${tw`lg:absolute text-grey-light z-10`};
  right: 6%;
  top: 0;
`

const Avatar = styled(Img)`
  ${tw`rounded-full w-32 h-32 shadow-lg m-auto`};
`

const Section = ({ children, ...props }) => {
  const { name, title, image } = props

  return (
    <SectionContainer>
      <SectionHeader>
        <Avatar alt={name} fluid={image} />

        <Name>
          <Highlight>{name}</Highlight>
        </Name>
      </SectionHeader>
      <Title>{title}</Title>

      <SectionContents>{children}</SectionContents>
      <Separator />
    </SectionContainer>
  )
}

Section.propTypes = {
  children: PropTypes.node.isRequired,
  name: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  image: PropTypes.any.isRequired,
}

const MembersList = data => {
  const { images, datafromjson } = data.data

  return (
    <>
      {datafromjson.edges.map(item => {
        const imageIndex = images.edges.findIndex(
          x =>
            x.node.name ===
            item.node.image
              .split(".")
              .slice(0, -1)
              .join(".")
        )
        return (
          <Section
            key={imageIndex}
            name={item.node.name}
            title={item.node.title}
            image={images.edges[imageIndex].node.childImageSharp.fluid}
          >
            <P>{item.node.bio}</P>
          </Section>
        )
      })}
    </>
  )
}

const Team = () => (
  <StaticQuery
    query={graphql`
      query AllTeamMemberImagesJSON {
        images: allFile(filter: { absolutePath: { regex: "/avatars/" } }) {
          edges {
            node {
              name
              childImageSharp {
                fluid(maxWidth: 400) {
                  ...GatsbyImageSharpFluid
                }
              }
            }
          }
        }
        datafromjson: allTeamMembersJson {
          edges {
            node {
              name
              title
              bio
              image
            }
          }
        }
      }
    `}
    render={data => (
      <Layout>
        <SEO title="The Team" />
        <AppWrapper>
          <Hero isDark>
            <CloseWrapper style={{ opacity: 0 }}>
              <LinkTo href="/">
                <CloseIcon />
              </LinkTo>
            </CloseWrapper>

            <BigTitle style={{ opacity: 0 }}>
              the
              <Highlight> Featurists</Highlight>
            </BigTitle>
            <PLight style={{ opacity: 0 }} />
          </Hero>
          <Content>
            <SectionWrapper>
              <MembersList data={data} />
            </SectionWrapper>
          </Content>
        </AppWrapper>
      </Layout>
    )}
  />
)

export default Team
